// ========== НАЧАЛО СТИЛЕЙ ДЛЯ МОБИЛ ==========

.program {
  position: relative;
  background: $primary-text-color;
  padding-top: 50px;
}
.program__container {
  padding-bottom: 540px;
}
.program__title {
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03em;
  text-align: center;
  color: $primary-background;
}
.program__list {
  margin-top: 45px;
  padding-left: 75px;
  position: relative;
}
.program__list-title {
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  padding-left: 25px;
  color: $primary-background;
  line-height: 1;
  position: relative;
  &::before {
    content: '';
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $accent;
    position: absolute;
    left: -6px;
    top: 0;
  }
  &:not(:last-child) {
    padding-bottom: 40px;
  }
  &:not(:last-child) {
    border-left: 1px solid $primary-background;
  }
}
.program__list-subtitle {
  margin-top: 15px;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.66;
  // display: flex;
  align-items: center;
  text-transform: capitalize;
  color: $accent;
  &::before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 1px;
    margin-right: 6px;
    background-color: $accent;
  }
}
.program__list-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.66;
  color: $secondary-text-color;
  opacity: 0.5;
}
.distance {
  margin-top: 15px;
}
@media screen and (max-width: 767px) {
  .program__list-subtitle:nth-child(1) {
    width: 190px;
  }
  .program__list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 767px) {
  .list-foto::after {
    content: '';
    position: absolute;
    left: 110px;
    top: 1310px;
    width: 120px;
    height: 137px;
    background-repeat: no-repeat;
    opacity: 0.5;
    background-image: url(../images/project_mobile/program_mobile.jpg);
    background-position: center;
    background-size: cover;
  }
}
@media (min-device-pixel-ratio: 2) and (max-width: 767px),
  (min-resolution: 192dpi) and (max-width: 767px),
  (min-resolution: 2dppx) and (max-width: 767px) {
  .list-foto::after {
    content: '';
    position: absolute;
    left: 110px;
    top: 1310px;
    width: 120px;
    height: 137px;
    background-repeat: no-repeat;
    opacity: 0.5;
    background-image: url(../images/project_mobile/program_mobile@2x.jpg);
    background-position: center;
    background-size: cover;
  }
}

// ========== КОНЕЦ СТИЛЕЙ ДЛЯ МОБИЛ ==========

// ========== НАЧАЛО СТИЛЕЙ ДЛЯ ТАБЛЕТ ==========

@media screen and (min-width: 768px) {
  .program__container {
    padding-bottom: 390px;
  }
  .program {
    padding-top: 60px;
  }
  .program__list-sublist {
    display: flex;
  }
  .program__list-subtitle {
    &:nth-child(1) {
      flex: none;
      width: 190px;
    }
    &:nth-child(2) {
      width: 155px;
    }
    &:nth-child(3) {
      width: 115px;
    }
    &:not(:first-child) {
      margin-left: 50px;
    }
  }
  .program__title {
    font-size: 26px;
    text-align: left;
  }
  .program__list {
    padding-left: 65px;
  }
  .program__list-title {
    padding-left: 55px;

    &:not(:last-child) {
      padding-bottom: 30px;
    }
  }
}
@media screen and (min-width: 768px) {
  .list-foto::after {
    content: '';
    position: absolute;
    left: 295px;
    top: 630px;
    width: 384px;
    height: 280px;
    background-repeat: no-repeat;
    opacity: 0.5;
    background-image: url(../images/project_tablet/program_tablet.jpg);
    background-position: center;
    background-size: cover;
  }
}
@media (min-device-pixel-ratio: 2) and (min-width: 768px),
  (min-resolution: 192dpi) and (min-width: 768px),
  (min-resolution: 2dppx) and (min-width: 768px) {
  .list-foto::after {
    content: '';
    position: absolute;
    left: 295px;
    top: 630px;
    width: 384px;
    height: 280px;
    background-repeat: no-repeat;
    opacity: 0.5;
    background-image: url(../images/project_tablet/program_tablet@2x.jpg);
    background-position: center;
    background-size: cover;
  }
}

// ========== КОНЕЦ СТИЛЕЙ ДЛЯ ТАБЛЕТ ==========

// ========== НАЧАЛО СТИЛЕЙ ДЛЯ ДЕСКТОП ==========

@media screen and (min-width: 1366px) {
  .program {
    padding-top: 120px;
  }
  .program__container {
    padding-bottom: 365px;
  }
  .program__title {
    font-size: 46px;
  }
  .div-border {
    border-top: 1px solid $primary-background;
    margin-top: 100px;
  }
  .program__list {
    margin-top: 0px;
    padding-left: 0px;
    display: flex;
    // border-top: 1px solid $primary-background;
  }
  .program__list-sublist {
    display: flex;
    flex-direction: column;
  }
  .program__list-subtitle {
    font-size: 20px;
    margin-top: 25px;
  }
  .distance {
    margin-top: 20px;
  }
  .program__list-subtitle:not(:first-child) {
    margin-left: 0px;
  }
  .program__list-subtitle {
    &:nth-child(1) {
      flex: none;
      width: 100%;
    }
    &:nth-child(2) {
      width: 100%;
    }
    &:nth-child(3) {
      width: 100%;
    }
  }
  .program__list-text {
    font-size: 18px;
  }
  .program__list-title {
    font-size: 20px;
    padding-left: 0px;
    margin-top: 60px;
    width: 240px;
    &:not(:first-child) {
      margin-left: 30px;
    }
    &::before {
      content: '';
      display: inline-block;
      width: 17.14px;
      height: 16px;
      border-radius: 50%;
      background-color: $accent;
      position: absolute;
      left: -2px;
      top: -69px;
    }
    &:not(:last-child) {
      padding-bottom: 0px;
    }
  }
  .program__list-title:not(:last-child) {
    // border-top: 1px solid $primary-background;
    border-left: none;
  }
}
@media screen and (min-width: 1366px) {
  .list-foto::after {
    content: '';
    position: absolute;
    left: -270px;
    top: 360px;
    width: 760px;
    height: 502px;
    background-repeat: no-repeat;
    opacity: 0.5;
    background-image: url(../images/project_desktop/program_desktop.jpg);
    background-position: center;
    background-size: cover;
  }
}
@media (min-device-pixel-ratio: 2) and (min-width: 1366px),
  (min-resolution: 192dpi) and (min-width: 1366px),
  (min-resolution: 2dppx) and (min-width: 1366px) {
  .list-foto::after {
    content: '';
    position: absolute;
    left: -270px;
    top: 360px;
    width: 760px;
    height: 502px;
    background-repeat: no-repeat;
    opacity: 0.5;
    background-image: url(../images/project_desktop/program_desktop@2x.jpg);
    background-position: center;
    background-size: cover;
  }
}

